import React, { Component } from 'react';
import { number } from 'prop-types';

// Components
import SchedulerHeader from '../SchedulerHeader/index';
import SchedulerBody from '../SchedulerBody/index';
import SchedulerFooter from '../SchedulerFooter/index';
import Spinner from '../Spinner/index';

// Styles
import Styles from './styles.m.css';

// Instruments
import { getTaskIndex, sortTasksByDate } from '../../instruments/helpers';
import { Api } from '../../instruments/api';

class Scheduler extends Component {
    constructor (props) {
        super(props);
        this.state = {
            tasks:        [],
            messageValue: '',
            filterTask:   '',
            allCompleted: false,
            isLoading:    false,
        };
    }

    componentWillMount () {
        this.isLoading(true);
        Api.get()
            .then((response) => response.json())
            .then(({ data }) => {
                this.updateTasks(data);
                this.isLoading(false);
            })
            .catch((error) => console.log(error));
    }

    isLoading (value) {
        this.setState({
            isLoading: value,
        });
    }

    updateTasks (tasks) {
        if (tasks.length === 0) {
            return;
        }
        let sortedTasks = [];
        const favorite = [];
        const completed = [];
        const other = [];

        tasks.sort(sortTasksByDate);
        const tasksCompleted = tasks.every((task) => task.completed);

        tasks.forEach((task) => {
            if (task.favorite && !task.completed) {
                favorite.push(task);
            }
            if (task.completed) {
                completed.push(task);
            }
            if (!task.favorite && !task.completed) {
                other.push(task);
            }
        });
        sortedTasks = [...favorite, ...other, ...completed];

        if (!this.state.allCompleted && !tasksCompleted) {
            this.setState({
                tasks: sortedTasks,
            });
        } else if (tasksCompleted) {
            this.setState({
                tasks:        sortedTasks,
                allCompleted: true,
            });
        } else {
            this.setState({
                tasks:        sortedTasks,
                allCompleted: false,
            });
        }
    }

    handleMessageChange = (messageValue) => {
        this.setState({
            messageValue,
        });
    }

    handlerSubmit = () => {
        const message = this.state.messageValue;

        if (message.trim() === '') {
            return;
        }
        this.isLoading(true);
        Api.post('', {
            message,
            completed: false,
            favorite:  false,
            created:   new Date(),
        })
            .then((response) => response.json())
            .then(({ data }) => {
                this.setState((prevState) => ({
                    tasks:        [data, ...prevState.tasks],
                    messageValue: '',
                    allCompleted: false,
                    isLoading:    false,
                }));
            })
            .catch((error) => console.log(error));

    }

    handleSortTasks = (property, id) => {
        const tasks = [...this.state.tasks];
        const index = getTaskIndex(tasks, id);

        tasks[index][property] = !tasks[index][property];
        this.isLoading(true);
        Api.update('', tasks)
            .then((response) => response.json())
            .then(({ data }) => {
                this.updateTasks(data);
                this.isLoading(false);
            })
            .catch((error) => console.log(error));
    }


    handleRemoveTask = (id) => {
        this.isLoading(true);
        Api.delete()
            .then((response) => response)
            .then((data) => {
                if (data.status === 204) {
                    const tasks = [...this.state.tasks];
                    const index = getTaskIndex(tasks, id);

                    tasks.splice(index, 1);
                    this.setState({
                        tasks,
                        isLoading: false,
                    });
                }
            })
            .catch((error) => console.log(error));
    }

    handleKeyPressed = (id, message) => {
        const tasks = [...this.state.tasks];
        const index = getTaskIndex(tasks, id);

        tasks[index].message = message;
        this.isLoading(true);
        Api.update('', tasks)
            .then((response) => response.json())
            .then(({ data }) => {
                this.updateTasks(data);
                this.isLoading(false);
            })
            .catch((error) => console.log(error));
    }

    handleCompletedTasks = () => {
        const tasks = [...this.state.tasks];
        const tasksCompleted = tasks.every((task) => task.completed);

        if (tasksCompleted) {
            return;
        }
        tasks.forEach((task) => {
            task.completed = true;
        });
        this.isLoading(true);
        Api.update('', tasks)
            .then((response) => response.json())
            .then(({ data }) => {
                this.updateTasks(data);
                this.isLoading(false);
            })
            .catch((error) => console.log(error));
    }

    handleSearchTask = (filterTask) => {
        this.setState({
            filterTask,
        });
    }

    render () {
        const { filterTask, tasks, messageValue, allCompleted, isLoading } = this.state;

        return (
            <div className = { Styles.scheduler }>
                <main>
                    <SchedulerHeader
                        filterTask = { filterTask }
                        handleSearchTask = { this.handleSearchTask }
                    />
                    <SchedulerBody
                        className = { Styles.overlay }
                        filterTask = { filterTask }
                        handleKeyPressed = { this.handleKeyPressed }
                        handleMessageChange = { this.handleMessageChange }
                        handleRemoveTask = { this.handleRemoveTask }
                        handlerSubmit = { this.handlerSubmit }
                        handleSortTasks = { this.handleSortTasks }
                        maxLength = { this.props.maxLength }
                        messageValue = { messageValue }
                        tasks = { tasks }
                    />
                    <SchedulerFooter
                        allCompleted = { allCompleted }
                        className = { Styles.completeAllTasks }
                        handleCompletedTasks = { this.handleCompletedTasks }
                    />
                    <Spinner spin = { isLoading } />
                </main>
            </div>
        );
    }
}

Scheduler.propTypes = {
    maxLength: number,
};

export default Scheduler;
